body.swal2-in {
    overflow-y: hidden;
}

body.swal2-iosfix {
    position: fixed;
    left: 0;
    right: 0;
}

.swal2-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 10px;
    background-color: transparent;
    z-index: 1060;
}

.swal2-container:not(.swal2-in) {
    pointer-events: none;
}

.swal2-container.swal2-fade {
    -webkit-transition: background-color .1s;
    transition: background-color .1s;
}

.swal2-container.swal2-in {
    background-color: rgba(0, 0, 0, 0.4);
}

.swal2-modal {
    background-color: $white-color;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    margin: auto;
    overflow-x: hidden;
    overflow-y: auto;
    display: none;
    position: relative;

    button{
        margin: 0 5px;
    }

    .form-control{
        max-width: 300px;
        margin: 0 auto;
    }
}

.swal2-modal:focus {
    outline: none;
}

.swal2-modal.swal2-loading {
    overflow-y: hidden;
}

.swal2-modal .swal2-title {
    color: #595959;
    font-size: 30px;
    text-align: center;
    font-weight: 600;
    text-transform: none;
    position: relative;
    margin: 0;
    padding: 0;
    line-height: 60px;
    display: block;
}

.swal2-modal .swal2-spacer {
    height: 10px;
